import React, { useEffect, useState } from 'react';
import './style.css';

const Selectfield = ({ label, options }) => {
	const [currentSelectedOption, setCurrentSelectedOption] = useState('');

	class Select {
		constructor(element) {
			this.element = element;
			this.options = makeOptionObject(element.querySelectorAll('option'));
			this.customElement = document.createElement('div');
			this.labelElement = document.createElement('span');
			this.optionsCustom = document.createElement('ul');
			setupSelect(this);
			element.after(this.customElement);
		}

		get selectedOption() {
			return this.options.find((option) => option.selected);
		}

		get selectedOptionIndex() {
			return this.options.indexOf(this.selectedOption);
		}

		selectValue(value) {
			const newSelectedOption = this.options.find((option) => {
				return option.value === value;
			});
			const prevSelectedOption = this.selectedOption;
			prevSelectedOption.selected = false;
			prevSelectedOption.element.selected = false;

			newSelectedOption.selected = true;
			newSelectedOption.element.selected = true;

			this.labelElement.innerText = newSelectedOption.value;
			this.optionsCustom.querySelector(`[data-value="${prevSelectedOption.value}"]`).classList.remove('selected');
			const newCustomElement = this.optionsCustom.querySelector(`[data-value="${newSelectedOption.value}"]`);
			newCustomElement.classList.add('selected');
			newCustomElement.scrollIntoView({ block: 'nearest' });
			setCurrentSelectedOption(value);
		}
	}

	const setupSelect = (select) => {
		select.customElement.classList.add('select-custom-container');
		select.customElement.tabIndex = 0;

		select.labelElement.classList.add('select-custom-value');
		select.labelElement.innerText = select.selectedOption.label;
		select.customElement.append(select.labelElement);

		select.optionsCustom.classList.add('select-custom-options');
		select.options.forEach((option) => {
			const optionElement = document.createElement('li');
			optionElement.classList.add('select-custom-option');
			optionElement.classList.toggle('selected', option.selected);
			optionElement.innerText = option.label;
			optionElement.dataset.value = option.value;
			optionElement.addEventListener('click', () => {
				select.selectValue(option.value);
				optionElement.classList.add('selected');
				select.optionsCustom.classList.remove('show');
			});
			select.optionsCustom.append(optionElement);
		});
		select.customElement.append(select.optionsCustom);

		select.labelElement.addEventListener('click', () => {
			select.optionsCustom.classList.toggle('show');
		});

		// Hides the select options when clicked outside of the select element.
		select.customElement.addEventListener('blur', () => {
			select.optionsCustom.classList.remove('show');
		});
		let debounceTimeout;
		let searchTerm = '';
		select.customElement.addEventListener('keydown', (e) => {
			switch (e.code) {
				case 'Space':
					select.optionsCustom.classList.toggle('show');
					break;
				case 'ArrowUp':
					const prevOption = select.options[select.selectedOptionIndex - 1];
					if (prevOption) {
						select.selectValue(prevOption.value);
					}
					break;
				case 'ArrowDown':
					const nextOption = select.options[select.selectedOptionIndex + 1];
					if (nextOption) {
						select.selectValue(nextOption.value);
					}
					break;
				case 'Enter':
				case 'NumpadEnter':
				case 'Escape':
					select.optionsCustom.classList.remove('show');
					break;
				default:
					clearTimeout(debounceTimeout);
					searchTerm += e.key;
					debounceTimeout = setTimeout(() => {
						searchTerm = '';
					}, 500);
					const searchedOption = select.options.find((option) => {
						return option.label.toLowerCase().startsWith(searchTerm);
					});
					if (searchedOption) select.selectValue(searchedOption.value);
			}
		});
	};

	const makeOptionObject = (optionElements) => {
		// triple dot spreads out the iterable object to make an array, this enables the map method
		return [...optionElements].map((optionElement) => {
			return {
				value: optionElement.value,
				label: optionElement.label,
				selected: optionElement.selected,
				element: optionElement,
			};
		});
	};

	useEffect(() => {
		let select = document.querySelector('#yearselector');
		for (let option in options) {
			select.options[select.options.length] = new Option(options[option], option);
		}

		const selectElements = document.querySelectorAll('.select-custom');
		selectElements.forEach((selectElement) => {
			new Select(selectElement);
		});
	}, []);

	return (
		<div>
			<label>{label}</label>
			<select id='yearselector' className='select-custom'></select>
		</div>
	);
};

export default Selectfield;
